/* global chrome */
import React, { useState, useEffect } from "react";
import "./App.css";
import "./Add";
import Add from "./Add";
import Article from "./Article";

function App() {
  const [articles, setArticles] = useState([]);
  useEffect(() => {
    chrome.storage.sync.get("list", items => {
      setArticles(items.list);
    });
  }, []);
  return (
    <div className="App">
      <div className="Search__Container"></div>
      <div className="Article__Container">
        {articles.reverse().map((data, i) => (<Article key={i} data={data} />))}
      </div>
      <div className="Button__Container">
        <Add />
      </div>
    </div>
  );
}

export default App;
