/*global chrome*/
import React from "react";
const Add = () => {
  return (
    <div className="Button__Wrapper">
      <button
        className="Button__Self"
        onClick={() =>
          chrome.tabs.query({ active: true, currentWindow: true }, function(tabs) {
            chrome.storage.sync.get("list", items => {
              chrome.storage.sync.set({ list: [...items.list, { id: items.list.length, isRead: false, title: tabs[0].title, url: tabs[0].url }] });
            })
          })
        }
      >
        Add
      </button>
    </div>
  );
};

export default Add;
